.welcome-sedona {
  width: 320px;
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 768px) {
  .welcome-sedona {
    width: 768px;
    margin: 0 auto;
    text-align: center;
    box-sizing: border-box;
  }
}
@media (min-width: 1280px) {
  .welcome-sedona {
    width: 1200px;
    margin: 0 auto;
    text-align: center;
    box-sizing: border-box;
  }
}
.main-container__welcome-sedona {
  margin-bottom: 33px;
}
@media (min-width: 768px) {
  .main-container__welcome-sedona {
    margin-bottom: 62px;
  }
}
@media (min-width: 1280px) {
  .main-container__welcome-sedona {
    margin-bottom: 100px;
  }
}
.welcome-sedona__wrapper {
  background: url("/img/catalog/back-mobile-catalog.jpg") no-repeat;
  width: 320px;
  height: 214px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .welcome-sedona__wrapper {
    background: url("/img/catalog/back-tablet-catalog.jpg") no-repeat;
    width: 768px;
    height: 345px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
  }
  .welcome-sedona__wrapper .welcome-sedona__image {
    position: absolute;
    left: 20%;
    top: 45%;
  }
}
@media (min-width: 1280px) {
  .welcome-sedona__wrapper {
    background: url("/img/catalog/back-desktop-catalog.jpg") no-repeat;
    width: 1200px;
    height: 345px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
  }
  .welcome-sedona__wrapper .welcome-sedona__image {
    position: absolute;
    left: 31%;
    top: 45%;
  }
}
.welcome-sedona__tri {
  background: url("/img/intro-triangle.svg") no-repeat;
  width: 320px;
  height: 56px;
  position: absolute;
  bottom: 0;
}
@media (min-width: 768px) {
  .welcome-sedona__tri {
    background: url("/img/catalog/intro-triangle-tablet.svg") no-repeat;
    width: 768px;
    height: 56px;
    position: absolute;
    bottom: 0;
  }
}
@media (min-width: 1280px) {
  .welcome-sedona__tri {
    background: url("/img/catalog/intro-triangle-desktop.svg") no-repeat;
    width: 1200px;
    height: 56px;
    position: absolute;
    bottom: 0;
  }
}
